import { signOut } from '@firebase/auth';
import React, { useEffect, useState } from 'react';
import { getAllContacts } from '../../services/contactAction';
import { auth } from '../../services/firebase';
import ReactExport from "react-export-excel";
import styled from 'styled-components';
import _ from "lodash";
const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;

const Home = () =>{
      const [data, setData] = useState([])
      const [loading, setLoading] = useState(false)
      const signOutUser = async() =>{
        try{
          await signOut(auth).then(()=>{
            console.error("Se cerro session");
          })
        }catch(error){
          console.log(error)
        }
      }      

      useEffect(()=>{
        const getData = async()=>{
          const data  = await getAllContacts()
          setData(data)
          setLoading(true)
        }
        getData()
       
      },[])

      return(<>

        <div className="relative h-screen bg-gray-800">
          <nav className="bg-white">
            <div className="max-w-7xl mx-auto px-2 sm:px-6 lg:px-8">
              <div className="relative flex items-center justify-between h-16">
        
                <div className="flex-1 flex items-center justify-center sm:items-stretch sm:justify-start">
                  <div className="flex-shrink-0 flex items-center">
                    <img className="block lg:hidden h-8 w-auto" src="https://i.ibb.co/Zf0t30M/1.png" alt="Workflow"/>
                    <img className="hidden lg:block h-8 w-auto" src="https://i.imgur.com/9LPmT4m.png" alt="Workflow"/>
                  </div>
                  <div className="hidden sm:block sm:ml-6">
                    <div className="flex space-x-4">
                      
                    <span
                    onClick={()=>{
                      signOutUser()
                    }}
                     className="bg-gray-900 text-white px-3 py-2 rounded-md text-sm font-medium" aria-current="page">Cerrar Sesion</span>
        
                    </div>
                  </div>
                </div>
                
              </div>
            </div>
        
            <div className="sm:hidden" id="mobile-menu">
              <div className="px-2 pt-2 pb-3 space-y-1">
                
                <span className="bg-gray-900 text-white block px-3 py-2 rounded-md text-base font-medium" aria-current="page">Cerrar Sesion</span>
        
              </div>
            </div>
          </nav>     
          
        
          <div className="text-center">
          <p className="text-white text-lg font-bold my-5">Excel with contacts</p>
          {loading ? <div className="p-2 pl-5 pr-5 bg-transparent border-2 border-indigo-500 text-indigo-500 text-lg rounded-lg transition-colors duration-700 transform hover:bg-indigo-500 hover:text-gray-100 focus:border-4 focus:border-indigo-300">
            <ExcelFile>
                <ExcelSheet data={data} name="Contacts">
                    <ExcelColumn label="firstName" value="firstName"/>
                    <ExcelColumn label="lastName" value="lastName"/>
                    <ExcelColumn label="FirstName" value="firstName"/>
                    <ExcelColumn label="LastName" value="lastName"/>
                    <ExcelColumn label="City" value="city"/>
                    <ExcelColumn label="State Name" value="stateName"/>
                    <ExcelColumn label="ZIP" value="postalCode"/>
                    <ExcelColumn label="Email" value="email"/>
                    <ExcelColumn label="Phone" value="phone"/>
                    <ExcelColumn label="Source" value="source"/>
                    <ExcelColumn label="company" value="company"/>
                    <ExcelColumn label="Mobile phone" value="mobilephone"/>
                    <ExcelColumn label="Contact ID" value="hubspot_owner_id"/>
                    <ExcelColumn label="Owner Contact" value="ownerName"/>
                    <ExcelColumn label="Owner Id" value="hubspot_owner_id"/>
                    <ExcelColumn label="CallCenter Id" value="callCenterID"/>
                    <ExcelColumn label="CallCenter Agent" value="callCenterAgent"/>
                    <ExcelColumn label="Appointment" value="appointment"/>
                    <ExcelColumn label="Time" value="time"/>
                    <ExcelColumn label="Notes" value="notes"/>
                    <ExcelColumn label="preferredMethod" value="preferredMethod"/>
                    <ExcelColumn label="Language" value="language"/>
                    <ExcelColumn label="Qualified Appointment" value="qualified_appointment"/>
                    <ExcelColumn label="Audio" value="audio"/>
                    <ExcelColumn label="Contact ID" value="contactId"/>
                    <ExcelColumn label="Deal ID" value="dealId"/>
                    <ExcelColumn label="Deal Name" value="dealName"/>
                    <ExcelColumn label="Stage" value="stage"/>
                </ExcelSheet>
            </ExcelFile>
            </div> : <CustomLoader />}
          </div> 

          <section className="py-1 bg-blueGray-50">
          <div className="w-full xl:w-8/12 mb-12 xl:mb-0 px-4 mx-auto mt-24">
            <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 shadow-lg rounded ">
              <div className="rounded-t mb-0 px-4 py-3 border-0">
                <div className="flex flex-wrap items-center">
                  <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                    <h3 className="font-semibold text-base text-blueGray-700">Bright world</h3>
                  </div>
                 
                </div>
              </div>

          <div className="block w-full overflow-x-auto">
            <table className="items-center bg-transparent w-full border-collapse ">
              <thead>
                <tr>
                  <th className=" px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                  contactId           
                              </th>
                  <th className=" px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                  dealId          
                              </th>
                  <th className=" px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                  dealName       
                              </th>
                  <th className=" px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                  stage       
                              </th>
                </tr>
              </thead>

              <tbody>
                {_.map(data, (d, key)=>{
                  return (
                    <tr key={key}>
                      <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 ">{`${d.contactId}`}</td>
                      <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 ">{`${d.dealId}`}</td>
                      <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 ">{`${d.dealName}`}</td>
                      <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 ">{`${d.stage}`}</td>
                    </tr>
                  )
                })}
              </tbody>

            </table>

    </div>
  </div>
</div>

          </section>
            

        </div>
</>)
}
const CustomLoader = styled.div`
	border-radius: 60px;
	border: 3px solid #fff;
	height: 80px;
	width: 80px;
	position: relative;

	top: 28%;
	top: -webkit-calc(50% - 43px);
	top: calc(50% - 43px);
	left: 35%;
	left: -webkit-calc(50% - 43px);
	left: calc(50% - 43px);

:after{
	content: "";
	position: absolute;
	background-color: #fff;
	top:2px;
	left: 48%;
	height: 38px;
	width: 4px;
	border-radius: 5px;
	-webkit-transform-origin: 50% 97%;
			transform-origin: 50% 97%;
	-webkit-animation: grdAiguille 2s linear infinite;
			animation: grdAiguille 2s linear infinite;
}

@-webkit-keyframes grdAiguille{
    0%{-webkit-transform:rotate(0deg);}
    100%{-webkit-transform:rotate(360deg);}
}

@keyframes grdAiguille{
    0%{transform:rotate(0deg);}
    100%{transform:rotate(360deg);}
}

:before{
	content: "";
	position: absolute;
	background-color: #fff;
	top:6px;
	left: 48%;
	height: 35px;
	width: 4px;
	border-radius: 5px;
	-webkit-transform-origin: 50% 94%;
			transform-origin: 50% 94%;
	-webkit-animation: ptAiguille 12s linear infinite;
			animation: ptAiguille 12s linear infinite;
}

@-webkit-keyframes ptAiguille{
    0%{-webkit-transform:rotate(0deg);}
    100%{-webkit-transform:rotate(360deg);}
}

@keyframes ptAiguille{
    0%{transform:rotate(0deg);}
    100%{transform:rotate(360deg);}
}

`

export default Home 